import {
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CBadge,
  CFormSelect,
  CFormLabel,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import EDU_AXIOS from "src/helpers/edu_axios";
import MaterialTable from "material-table";
import notify from "src/helpers/notify";

function lesson_plan(props) {
  let school_id = props.match.params.school_id;
  let [data, setData] = useState([]);
  let [ev_classes, setEvClasses] = useState([]);
  let [s_class, setSClass] = useState("");

  let getSelected = (e) => {
    setSClass(e.target.value);
  };

  useEffect(() => {
    let url = process.env.REACT_APP_API_PATH + "/cm/get-class/" + school_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        setEvClasses(resp.data.data.classes);
      })

      .catch((error) => {
        console.log(error);
      });
  }, []);

  useEffect(() => {
    let url = `${process.env.REACT_APP_API_PATH}/cm/get-sections-by-class/${school_id}/${s_class}`;
    EDU_AXIOS.get(url)
      .then((resp) => {
        let sections = [];
        resp.data.data.map((sc) => {
          sections.push(s_class + "-" + sc.section_name);
        });
        let url1 = `${process.env.REACT_APP_API_PATH}/sm/get-subjects-by-class`;
        EDU_AXIOS.post(url1, {
          id: school_id,
          cl: sections,
          class: s_class,
        })
          .then((resp) => {
            console.log(resp.data.data);
            setData(resp.data.data);
          })

          .catch((error) => {
            console.log(error);
          });
      })

      .catch((error) => {
        console.log(error);
      });
  }, [s_class]);

  // add update lesson plan
  const lessonPlan = async (data) => {
    await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/sm/add-update-lesson-plan",
      {
        school_id: school_id,
        subject_id: data.subject_id,
        sc_class: data.class_name,
        min_periods: data.min_periods,
        max_periods: data.max_periods,
      }
    )
      .then(async (res) => {
        notify("success", res.data.message);
      })
      .catch((err) => {
        notify("error", err);
      });
  };
  const columns = [
    {
      title: "Class",
      field: "class_name",
      editable: "never",
    },
    {
      title: "Subject",
      field: "subject_name",
      editable: "never",
    },
    {
      title: "Min Periods",
      field: "min_periods",
      type: "numeric",
    },
    {
      title: "Max Periods",
      field: "max_periods",
      type: "numeric",
    },
  ];
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Lesson Plan</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Lesson Plan for Each Class</p>
          <CRow className="mb-3">
            <CFormLabel htmlFor="classes" className="col-sm-2 col-form-label">
              Select Class
            </CFormLabel>
            <CCol md={4}>
              <CFormSelect
                name="classes"
                id="classes"
                aria-label="Select class"
                onChange={(e) => {
                  getSelected(e);
                }}
              >
                <option>Select class...</option>

                {ev_classes.map((s) => {
                  return <option value={s}>{s}</option>;
                })}
              </CFormSelect>
            </CCol>
          </CRow>
          {/*Table Start*/}
          <MaterialTable
            title={""}
            data={data}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              actionsColumnIndex: -1,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
              /*pageSize: 15,
                emptyRowsWhenPaging: true,
                pageSizeOptions: [15, 30, 45],*/
            }}
            editable={{
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  console.log(newData);
                  setTimeout(() => {
                    const dataUpdate = [...data];
                    const index = oldData.tableData.id;
                    dataUpdate[index] = newData;
                    setData([...dataUpdate]);
                    lessonPlan(newData);
                    resolve();
                  }, 1000);
                }),
            }}
          />
          {/*Table End* */}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
}

export default lesson_plan;
